const {__} = wp.i18n;
const {Component, Fragment} = wp.element;

import btn1_light from './button-presets/btn-1/light.png';
import btn1_dark from './button-presets/btn-1/dark.png';

import btn3_moon_dark from './button-presets/btn-3/moon-dark.png';
import btn3_moon_light from './button-presets/btn-3/moon-light.png';
import btn3_sun_dark from './button-presets/btn-3/sun-dark.png';
import btn3_sun_light from './button-presets/btn-3/sun-light.png';

import btn4_light from './button-presets/btn-4/light.png';
import btn4_dark from './button-presets/btn-4/dark.png';

import btn5_light from './button-presets/btn-5/light.png';
import btn5_dark from './button-presets/btn-5/dark.png';

import btn6_light from './button-presets/btn-6/light.png';
import btn6_dark from './button-presets/btn-6/dark.png';

import btn7_light from './button-presets/btn-7/light.png';
import btn7_dark from './button-presets/btn-7/dark.png';

import btn8_light from './button-presets/btn-8/light.svg';
import btn8_dark from './button-presets/btn-8/dark.svg';

import btn9_light from './button-presets/btn-9/sun.svg';
import btn9_dark from './button-presets/btn-9/moon.svg';

import btn10_light from './button-presets/btn-10/sun.svg';
import btn10_dark from './button-presets/btn-10/moon.svg';

import btn11_light from './button-presets/btn-11/sun.svg';
import btn11_dark from './button-presets/btn-11/moon.svg';

import btn12_light from './button-presets/btn-12/sun.svg';
import btn12_dark from './button-presets/btn-12/moon.svg';

import btn13_light from './button-presets/btn-13/sun.svg';
import btn13_dark from './button-presets/btn-13/moon.svg';


class Button extends Component {

    render() {
        const {style} = this.props;

        return (
            <Fragment>

                <div className={`wp-dark-mode-switcher wp-dark-mode-ignore style-${style}`}>
                    {
                        style === 2 ?
                            <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                <div className="wp-dark-mode-ignore toggle"></div>
                                <div className="wp-dark-mode-ignore modes">
                                    <p className="wp-dark-mode-ignore light"></p>
                                    <p className="wp-dark-mode-ignore dark"></p>
                                </div>
                            </label>
                            : style === 3 ?
                            <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                <img className="sun-light" src={btn13_light} alt={__('Light')}/>
                                <div className="toggle wp-dark-mode-ignore"></div>
                                <img className="moon-light" src={btn13_dark} alt={__('Light')}/>

                            </label>
                            : style === 4 ?
                                <Fragment>

                                    <img className="wp-dark-mode-ignore sun-light" src={btn3_sun_light} alt={__('Light')}/>
                                    <img className="wp-dark-mode-ignore sun-dark" src={btn3_sun_dark} alt={__('Light')}/>
                                    <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                        <div className="wp-dark-mode-ignore toggle"></div>
                                    </label>
                                    <img className="wp-dark-mode-ignore moon-dark" src={btn3_moon_dark} alt={__('Dark')}/>
                                    <img className="wp-dark-mode-ignore moon-light" src={btn3_moon_light} alt={__('Dark')}/>

                                </Fragment>
                                : style === 5 ?
                                    <Fragment>
                                        <p className='wp-dark-mode-ignore'>{__('Light')}</p>
                                        <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                            <div className="wp-dark-mode-ignore modes">
                                                <img className="wp-dark-mode-ignore light" src={btn4_light} alt={__('Light')}/>
                                                <img className="wp-dark-mode-ignore dark" src={btn4_dark} alt={__('Dark')}/>
                                            </div>
                                        </label>
                                        <p>{__('Dark')}</p>
                                    </Fragment>
                                    :
                                    style === 6 ?
                                        <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                            <div className="wp-dark-mode-ignore modes">
                                                <img className="wp-dark-mode-ignore light" src={btn5_light} alt={__('Light')}/>
                                                <img className="wp-dark-mode-ignore dark" src={btn5_dark} alt={__('Dark')}/>
                                            </div>
                                        </label>
                                        :
                                        style === 7 ?
                                            <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                                <div className="wp-dark-mode-ignore modes">
                                                    <img className="wp-dark-mode-ignore light" src={btn6_light} alt={__('Light')}/>
                                                    <img className="wp-dark-mode-ignore dark" src={btn6_dark} alt={__('Dark')}/>
                                                </div>
                                            </label>
                                            : style === 8 ?
                                            <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                                <div className="wp-dark-mode-ignore toggle"></div>
                                                <div className="wp-dark-mode-ignore modes">
                                                    <img className="wp-dark-mode-ignore light" src={btn7_light} alt="Light"/>
                                                    <img className="wp-dark-mode-ignore dark" src={btn7_dark} alt="Dark"/>
                                                </div>
                                            </label>
                                            : style === 9 ?
                                                <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                                    <div className="modes wp-dark-mode-ignore">
                                                        <img className="light" src={btn8_light} alt={__('Light')}/>
                                                        <img className="dark" src={btn8_dark} alt={__('Dark')}/>
                                                    </div>
                                                </label>
                                                : style === 10 ?
                                                    <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                                        <div className="modes wp-dark-mode-ignore">
                                                            <img className="light" src={btn9_light} alt={__('Light')}/>
                                                            <img className="dark" src={btn9_dark} alt={__('Dark')}/>
                                                        </div>
                                                    </label>
                                                    : style === 11 ?
                                                        <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                                            <div className="modes wp-dark-mode-ignore">
                                                                <img className="light" src={btn10_light} alt={__('Light')}/>
                                                                <img className="dark" src={btn10_dark} alt={__('Dark')}/>
                                                            </div>
                                                        </label>
                                                        : style === 12 ?
                                                            <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                                                <div className="modes wp-dark-mode-ignore">
                                                                    <img className="light" src={btn11_light} alt={__('Light')}/>
                                                                    <img className="dark" src={btn11_dark} alt={__('Dark')}/>
                                                                </div>
                                                            </label>
                                                            : style === 13 ?
                                                                <label htmlFor="wp-dark-mode-switch" className="wp-dark-mode-ignore">
                                                                    <img className="sun-light" src={btn12_light} alt={__('Light')}/>
                                                                    <div className="toggle wp-dark-mode-ignore"></div>
                                                                    <img className="moon-light" src={btn12_dark} alt={__('Dark')} />
                                                                </label>

                                                                :
                                                                <label className='wp-dark-mode-ignore' htmlFor="wp-dark-mode-switch">
                                                                    <div className="wp-dark-mode-ignore modes">
                                                                        <img className="wp-dark-mode-ignore light" src={btn1_light} alt={__('Light')}/>
                                                                        <img className="wp-dark-mode-ignore dark" src={btn1_dark} alt={__('Dark')}/>
                                                                    </div>
                                                                </label>
                    }

                </div>

            </Fragment>

        )
    }
}

export default Button;